@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Raleway&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

/* font-family: 'Open Sans', sans-serif;
font-family: 'Raleway', sans-serif; */

* {
  font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4 {
  font-family: "Raleway", sans-serif;
  @apply font-bold;
}

h1 {
  @apply text-2xl sm:text-4xl font-bold;
}
